<!-- 子组件 -->
<template>
  <div>
    <p>props a: {{ a }}</p>
    <p>$attrs: {{ $attrs }}</p>
  </div>
</template>

<script>
  export default {
    props: {
      a: {
        type: String,
        default: () => (''),
      },
    },
    mounted() {
      console.log(this.$attrs); // { "b": "2", "c": "3" }
    },
  }
</script>
